<template>
  <div class="task-container">
    <table class="tasks-table">
      <tr v-for="task in tasks" :key="task.id">
        <td style="padding-left: 20px;">{{ task.id }}.</td>
        <td style="padding-right: 120px;">{{ task.title }}</td>
        <td style="padding-left: 120px;">{{ task.date }}</td>
      </tr>
    </table>
    <form class="task-form" @submit.prevent>
      <div
        class="task-creater"
        v-if="showTaskCreaterFields"
      >
        <textarea
          class="textarea"
          placeholder=" ..."
          v-model="form.task"
        />
        <ScrollSelect
          class="start-date select"
          placeholder=" "
          v-model="form.startDate"
        />
        <ScrollSelect
          class="finish-date select"
          placeholder=" "
          v-model="form.finishDate"
        />
      </div>
      <div class="buttons">
        <button
          class="buttons__add-task basic-button"
          @click="showTaskCreaterFields = !showTaskCreaterFields"
          v-if="!showTaskCreaterFields"
        >
           
        </button>
        <button
          class="buttons__save-task basic-button"
          @click="saveNewTask"
          v-if="showTaskCreaterFields"
        >
           
        </button>
        <button
          class="buttons__save basic-button"
        >
          
        </button>
      </div>
    </form>
  </div>
</template>

<script>
import ScrollSelect from '../../../ScrollSelect'

export default {
  components: {
    ScrollSelect
  },
  data: () => ({
    form: {
      task: '',
      startDate: '',
      finishDate: ''
    },
    tasks: [
      {
        id: '1',
        title: '    4',
        date: '18.02.2020',
        author: ' '
      },
      {
        id: '2',
        title: '    4C (2 )',
        date: '20.11.2020',
        author: ' '
      },
      {
        id: '3',
        title: '    5C (5 )',
        date: '18.02.2020',
        author: ' '
      }
    ],
    showTaskCreaterFields: false
  }),
  methods: {
    saveNewTask () {
      this.showTaskCreaterFields = !this.showTaskCreaterFields
      console.log('task saved!')
    }
  }
}
</script>

<style lang="scss" scoped>

.task-container {
  color: #323A52;
  padding-top: 40px;
  padding-bottom: 20px;
}

.tasks-table {
  border-collapse: collapse;
  font-size: 14px;
  text-transform: uppercase;
  width: 100%;
}

.tasks-table td, .tasks-table th {
  border-bottom: 1px solid #e0e2e5;
  padding-top: 12px;
  padding-bottom: 12px;
  padding-left: 20px;
  padding-right: 20px;
}

table tr:last-child td {
  border-bottom: 0;
}

.tasks-table tr:hover {
  background-color: #f3f3f3;
  cursor: pointer;
}

tr {
  font-size: 14px;
}

.task-creater {
  display: grid;
  box-sizing: border-box;
  padding-top: 10px;
  padding: 12px;
  column-gap: 16px;
  row-gap: 8px;
  grid-template-areas: 'textarea start-date'
                        'textarea finish-date';
  .textarea {
    grid-area: textarea;
    display: block;
    box-sizing: border-box;
    height: 108px;
    min-height: 108px;
    max-height: 200px;
    max-width: 560px;
    min-width: 560px;
    border-radius: 10px;
    border: 2px solid #e0e2e5;
    font-weight: bold;
    font-size: 14px;
    padding: 16px;
    margin: 0;
    outline: none;
  }
  .start-date {
    grid-area: start-date;
    width: 100%;
  }
  .finish-date {
    grid-area: finish-date;
    width: 100%;
    align-self: end;
  }
}

.buttons {
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 20px 0;
  &__add-task {
    background-color: #F87762;
    &:hover {
      background-color: #f65e46;
    }
    &:active {
      background-color: #e93a20;
    }
  }
  &__save-task {
    background-color: #F87762;
    &:hover {
      background-color: #f65e46;
    }
    &:active {
      background-color: #e93a20;
    }
  }
  &__save {
    background-color: #44CC95;
    &:hover {
      background-color: #2fe099;
    }
    &:active {
      background-color: #1cfaa1;
    }
  }
}

.basic-button {
  height: 50px;
  padding: 0 32px;
  box-sizing: border-box;
  border: none;
  border-radius: 100px;
  color: white;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 14px;
  outline: none;
  cursor: pointer;
  margin: 0 8px;
  text-align: center;
}

.select ::v-deep .search-input {
  height: 50px;
  border-radius: 100px;
  border: 2px solid #e0e2e5;
  padding: 0 20px;
  font-size: 14px;
  font-weight: bold;
  text-transform: uppercase;
}

.select ::v-deep .selected {
  border: 2px solid #e0e2e5;
}

.select ::v-deep .search-input::-webkit-input-placeholder {
  text-transform: uppercase;
  font-weight: bold;
  font-size: 14px;
  color: #999da9;
}

.select ::v-deep .search-input:-ms-input-placeholder {
  text-transform: uppercase;
  font-weight: bold;
  font-size: 14px;
  color: #999da9;
}

.select ::v-deep .search-input::placeholder{
  text-transform: uppercase;
  font-weight: bold;
  font-size: 14px;
  color: #999da9;
}
</style>
