<template>
  <transition name="modal">
    <div class="modal-mask" @click="close" v-show="show">
      <div class="form-container" @click.stop>
        <div class="form-container__option" @click="settings"></div>
        <div class="form-container__option" @click="help"></div>
        <div class="form-container__option logout" @click="logOut"></div>
      </div>
    </div>
  </transition>
</template>

<script>
export default {
  props: ['show'],
  methods: {
    close () {
      this.$emit('close')
    },
    settings () {
      alert('settings')
    },
    help () {
      alert('help')
    },
    logOut () {
      alert('log out')
    }
  }
}
</script>

<style lang="scss" scoped>
.modal-mask {
 position: fixed;
  z-index: 999;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 10px;
  transition: opacity .3s ease;
  background: rgba(50, 58, 82, 0.35);
  backdrop-filter: blur(1px);
}

.form-container {
  z-index: 1000;
  width: 140px;
  margin: 25% auto 0;
  padding: 20px 20px;
  color: #323A52;
  border-radius: 10px;
  background-color: white;
  box-sizing: border-box;
  box-shadow: 0 2px 8px rgba(0, 0, 0, .33);
  transition: all .3s ease;
  position: relative;
  right: 480px;
  top: 280px;
  &__option {
    padding: 4px 0;
    cursor: pointer;
    font-size: 12px;
    transition: color 0.1s;
    text-transform: uppercase;
    &:hover {
      color: #6a7db1;
    }
  }
  .logout {
    padding-top: 12px;
  }
}

.modal-body {
  margin: 20px 0;
}

.modal-enter {
  opacity: 0;
}

.modal-leave-active {
  opacity: 0;
}

.modal-enter .modal-container,
.modal-leave-active .modal-container {
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
}
</style>
