<template>
  <label class="checkbox-container">
    <input
      :checked="isChecked"
      @click="handleClick()"
      type="checkbox"
    />
    <span class="checkmark"></span>
  </label>
</template>

<script>
export default {
  model: {
    prop: 'checked',
    event: 'change'
  },
  props: {
    checked: null,
    value: {
      type: Boolean,
      default: true
    }
  },
  computed: {
    isChecked () {
      return this.checked === this.value
    }
  },
  methods: {
    handleClick () {
      setTimeout(() => {
        if (this.isChecked) {
          this.$emit('change', false)
        } else {
          this.$emit('change', this.value)
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>
$Shades-of-Royal-Blue-1-hex: #0554F2;
$Shades-of-Royal-Blue-2-hex: #044BD9;
$Shades-of-Royal-Blue-3-hex: #0339A6;
$Shades-of-Royal-Blue-4-hex: #4CB1F7;
$Shades-of-Royal-Blue-5-hex: #0233CB;
$Dark-blue: #16243d;
$Soft-white: #f5f7fa;
$Middle-gray: #58647a;

.checkbox-container {
  color: $Dark-blue;
  display: block;
  position: relative;
  padding-left: 32px;
  cursor: pointer;
  font-size: 16px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.checkbox-container input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

.checkmark {
  position: absolute;
  top: -4px;
  left: 0;
  height: 22px;
  width: 22px;
  border-radius: 6px;
  border: 3px solid #e0e2e5;
  background-color: white;
}

.checkbox-container input:checked ~ .checkmark {
  // background-color: $Shades-of-Royal-Blue-1-hex;
}

.checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

.checkbox-container input:checked ~ .checkmark:after {
  display: block;
}

.checkbox-container .checkmark:after {
  left: 8px;
  top: 4px;
  width: 4px;
  height: 10px;
  border: solid #323A52;
  border-width: 0 3px 3px 0;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
}
</style>
