<template>
  <div class="user-info-container">
    <div class="user-photo" :style="{ backgroundColor: generateRandomColor }">{{ getInitials }}</div>
    <div class="user-info">
      <div class="first-row">
        <div class="user-info__user-name">{{ userName }}</div>
          <div
            class="arrow-menu"
            :class="{ 'rotated-left': showModal }"
            @click="showModal = true"
          >
            <svg width="17" height="10" viewBox="0 0 17 10" fill="none" xmlns="http://www.w3.org/2000/svg">
              <path fill-rule="evenodd" clip-rule="evenodd" d="M9.95001 8.26864L9.95014 8.26878L8.77516
                9.45L0.550293 1.18148L1.72527 0.000260925L8.77502 7.08743L15.825 0L17
                1.18122L9.95001 8.26864Z" fill="white"/>
            </svg>
          </div>
          <Options
            :show="showModal"
             @close="showModal = false"
          />
        </div>
      <div class="user-info__user-position">{{ userPosition }}</div>
    </div>
  </div>
</template>

<script>
import Options from './Options'

export default {
  components: {
    Options
  },
  data: () => ({
    userName: ' ',
    userPosition: '',
    showModal: false,
    rotated: false
  }),
  computed: {
    generateRandomColor () {
      const colors = ['#FAAB1D', '#6C6DE5', '#F8654C', '#44CC95', '#00B4CC', '#CA0345', '#18A255', '#D4BD92', '#F87762']
      return colors[Math.floor(Math.random() * colors.length)]
    },
    getInitials () {
      return this.userName.match(/[^-\s]/gm).join().replace(/,/, '')
    }
  }
}
</script>

<style lang="scss" scoped>
.user-info-container {
  display: flex;
}

.user-photo {
  display: flex;
  align-items: center;
  text-align: center;
  justify-content: center;
  width: 65px;
  height: 65px;
  border-radius: 50%;
  color: white;
  text-transform: uppercase;
  font-weight: 600;
  font-size: 1.5rem;
  border: none;
}

.user-info {
  margin-top: 0.6rem;
  margin-left: 1rem;
  &__user-name {
    font-size: 1rem;
    font-weight: 600;
    color: white;
  }
  &__user-position {
    margin-top: 0.4rem;
    font-size: 0.9rem;
    font-weight: 500;
    color: white;
  }
}

.first-row {
  display: flex;
}

.arrow-menu {
  margin-left: 12px;
  cursor: pointer;
  transform: rotate(0deg);
  transition: 0.6s ease-out;
}

.rotated-left {
  transform: rotate(90deg);
  transition: 0.3s ease;
}
</style>
