<template>
  <div class="pie-chart" :style="pieChartStyle">
  </div>
</template>

<script>
export default {
  props: [
    'pieChartData'
  ],
  computed: {
    pieChartStyle () {
      let percent = 0
      const styles = this.pieChartData.map(
        piePart => {
          const gradientData = `${piePart.color} 0 ${percent += piePart.value}%`
          return gradientData
        }
      )
      return {
        background: 'conic-gradient(' + styles.join(',') + ')'
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.pie-chart {
  width: 260px;
  height: 260px;
  border-radius: 50%;
  align-content: center;
  text-align: center;
  align-items: center;
  justify-content: center;
}
</style>
