<template>
  <div class="work-schedule">
    <div class="schedule-header">
      <div class="schedule-header__schedule-name">  </div>
      <div class="schedule-header__in-fact-block">
        <div class="schedule-header__in-fact-block__info-circle"></div>
        <div class="schedule-header__in-fact-block__info-text">-  </div>
      </div>
      <div class="time-selecter">
        <Select
          :options="list"
          v-model="selected"
        />
      </div>
    </div>
    <div>
      <canvas class="workday-chart" width="600" height="400"></canvas>
    </div>
  </div>
</template>

<script>
import Chart from 'chart.js'
import Select from '../Select'

export default {
  components: {
    Select
  },
  data: () => ({
    selected: '',
    list: [
      '',
      '',
      ''
    ]
  }),
  mounted () {
    const ctx = document.getElementsByClassName('workday-chart')
    Chart.defaults.global.defaultFontColor = '#999da9'
    Chart.defaults.global.defaultFontFamily = 'Montserrat'
    Chart.defaults.global.defaultFontSize = 14
    Chart.defaults.scale.gridLines.zeroLineColor = 'transparent' // remove zero line of x axes
    const space = 28
    const workdayChart = new Chart(ctx, {
      type: 'line',
      data: {
        labels: ['8:00', '9:00', '10:00', '11:00', '12:00', '13:00', '14:00', '15:00', '16:00', '17:00', '18:00', '19:00', '20:00'],
        datasets: [{
          label: '# of Votes', // is hidden
          data: [12, 19, 3, 10, 2, 40, 12, 19, 3, 5, 20, 3, 60],
          backgroundColor: [
            '#6c6de526'
          ],
          borderColor: [
            '#6C6DE5'
          ],
          borderWidth: 2
        }]
      },
      options: {
        responsive: true,
        maintainAspectRatio: false,
        scales: {
          yAxes: [{
            barPercentage: 20,
            ticks: {
              beginAtZero: true,
              min: 0,
              max: 60,
              stepSize: 5,
              padding: space // add padding to chart`s left side
            },
            gridLines: {
              drawTicks: false, // remove extra lines width
              autoSkipPadding: 5,
              drawBorder: false // remove zero line of y axes
            }
          }],
          xAxes: [{
            gridLines: {
              display: false,
              color: '#323a5226',
              tickMarkLength: space // add spase to top side
            }
          }]
        },
        legend: { // hide lable block
          display: false
        },
        tooltips: { // hide lable block
          callbacks: {
            label: function (tooltipItem) {
              return tooltipItem.yLabel
            }
          }
        },
        layout: {
          padding: {
            top: 0,
            left: 0,
            right: 0,
            bottom: 0
          }
        }
      }
    })
    console.log(workdayChart)
  }
}
</script>

<style lang="scss" scoped>

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

.workday-chart {
  padding-top: 20px;
  padding-left: 40px;
  padding-right: 40px;
  padding-bottom: 60px;
}

canvas {
  width: 100% !important;
  height: 540px !important;
}

.work-schedule {
  width: 860px;
  height: 580px;
  background-color: #FFFFFF;
  border-radius: 10px;
  box-shadow: 0px 3px 15px
    rgba(101, 40, 30, 0.15);
}

.schedule-header {
  display: flex;
  justify-content: space-between;
  padding-top: 10px;
  padding-left: 40px;
  padding-right: 80px;
  text-transform: uppercase;
  &__schedule-name {
    display: flex;
    align-items: center;
    font-weight: 600;
    font-size: 16px;
    color: #323A52;
  }
  &__in-fact-block {
    display: flex;
    align-items: center;
    margin-left: 80px;
    &__info-circle {
      width: 12px;
      height: 12px;
      border-radius: 100%;
      background-color: #6C6DE5;
      border: none;
    }
    &__info-text {
      margin-left: 12px;
      font-weight: 500;
      font-size: 14px;
      color: #323A52;
    }
  }
}

.time-selecter ::v-deep .custom-select {
  width: 144px;
  line-height: 36px;
  font-size: 14px;
}

.time-selecter ::v-deep .selected {
  border: 2px solid #e0e2e5;
  padding-left: 20px;
  color: #323A52;
  font-weight: bold;
}

.time-selecter ::v-deep .selected-svg {
  margin-top: 3px;
}

.time-selecter ::v-deep .item {
  color: #323A52;
}
</style>
