<template>
  <div class="tables">
    <div class="tabs">
      <a @click="activeTab = 2" v-bind:class="[activeTab === 2 ? 'active' : '']"></a>
      <a @click="activeTab = 1" v-bind:class="[activeTab === 1 ? 'active' : '']"></a>
    </div>
    <div class="content">
      <table class="table" v-if="activeTab === 1">
        <tr>
          <th></th>
          <th style="text-align: center;">-</th>
        </tr>
        <tr v-for="data in info1" :key="data.id">
          <td>{{ data.name }}</td>
          <td style="text-align: center;">{{ data.amount }}</td>
        </tr>
        <tr>
          <td style="border-bottom: none; font-weight: bold;"></td>
          <td style="border-bottom: none; font-weight: bold; text-align: center;">{{ sum1 }}</td>
        </tr>
      </table>
      <table class="table" v-if="activeTab === 2">
        <tr>
          <th></th>
          <th style="text-align: center;">-</th>
        </tr>
        <tr v-for="data in info2" :key="data.id">
          <td>{{ data.name }}</td>
          <td style="text-align: center;">{{ data.amount }}</td>
        </tr>
        <tr>
          <td style="border-bottom: none; font-weight: bold;"></td>
          <td style="border-bottom: none; font-weight: bold; text-align: center;">{{ sum2 }}</td>
        </tr>
      </table>
    </div>
  </div>
</template>

<script>
export default {
  data: () => ({
    activeTab: 1,
    info1: [
      {
        name: '',
        amount: 1
      },
      {
        name: '',
        amount: 7
      },
      {
        name: '',
        amount: 4
      },
      {
        name: '',
        amount: 14
      },
      {
        name: '',
        amount: 8
      },
      {
        name: '',
        amount: 7
      }
    ],
    info2: [
      {
        name: '',
        amount: 12
      },
      {
        name: '',
        amount: 7
      },
      {
        name: '',
        amount: 9
      },
      {
        name: '',
        amount: 1
      },
      {
        name: '',
        amount: 8
      },
      {
        name: '',
        amount: 2
      }
    ],
    amounts1: [],
    sum1: 0,
    amounts2: [],
    sum2: 0
  }),
  mounted () {
    this.info1.forEach(i => {
      this.amounts1.push(i.amount)
    })
    for (let i = 0; i < this.amounts1.length; i++) {
      this.sum1 = this.sum1 + parseInt(this.amounts1[i])
    }
    this.info2.forEach(i => {
      this.amounts2.push(i.amount)
    })
    for (let i = 0; i < this.amounts2.length; i++) {
      this.sum2 = this.sum2 + parseInt(this.amounts2[i])
    }
  }
}
</script>

<style lang="scss" scoped>
$Dark-blue: #323A52;

.content {
  background-color: white;
}

.tabs a {
  color: #323a5280;
  float: right;
  cursor: pointer;
  font-weight: bold;
  margin-left: 16px;
  margin-top: 20px;
  margin-bottom: 24px;
}

.tabs a:nth-child(1) {
  margin-right: 32px;
}

.tabs a:hover {
  color: $Dark-blue;
  transition: color 0.6s;
}

.tabs a.active {
  color: $Dark-blue;
  cursor: default;
  border-bottom: 1px dashed $Dark-blue;
}

.table {
  border-collapse: collapse;
  width: 100%;
  font-size: 14px;
}

.table td, .table th {
  border-bottom: 1px solid #e0e2e5;
  padding-top: 14px;
  padding-bottom: 14px;
  padding-left: 40px;
  padding-right: 40px;
}

th {
  text-transform: uppercase;
}

.table tr:nth-child(even) {
  background-color: white;
}

table tr:last-child td {
  border-bottom: 0;
}

.table tr:hover {
  background-color: #f3f3f3;
}

.table th {
  padding-top: 12px;
  padding-bottom: 12px;
  text-align: left;
  background-color: white;
  color: $Dark-blue;
}
</style>
