<script>
import ScrollSelect from '../../../ScrollSelect'
import CheckBox from '../../../CheckBox'
import Swicher from '../../../Swicher'

export default {
  components: {
    ScrollSelect,
    CheckBox,
    Swicher
  },
  data: () => ({
    form: {
      workerFullName: '',
      companyName: '',
      workerPosition: '',
      jobSwicher: false,
      employmentDate: '',
      workerLocation: '',
      workerStatus: '',
      date: '',
      workerCitizenship: '',
      workerNationality: '',
      briefinghecked: false,
      registryhecked: false
    },
    list: [
      '1',
      '1',
      '1',
      '1'
    ],
    showAdditionalInfo: false
  })
}
</script>

<style lang="scss" scoped>
.worker-form {
  padding-top: 40px;
  padding-bottom: 20px;
  padding-left: 20px;
  padding-right: 20px;
}

.main-info {
  display: grid;
  gap: 20px;
  margin-bottom: 20px;
