<template>
  <div class="accident-prevention-container">
    <table class="safety-sheet-container">
      <tr>
        <th></th>
        <th> </th>
        <th></th>
        <th></th>
      </tr>
      <tr v-for="data in safetySheet" :key="data.id">
        <td>
          <CheckBox
            class="checkbox"
            v-model="data.isChecked"
          />
        </td>
        <td>{{ data.typeOfSafety }}</td>
        <td style="padding-right: 60px">{{ data.safetyOfficer }}</td>
        <td>{{ data.date }}</td>
      </tr>
    </table>
    <form @submit.prevent>
      <div
        class="instruction-from"
        v-if="showInstruction"
      >
        <ScrollSelect
          class="select"
          placeholder="  "
          :options="testList"
          v-model="form.typeOfSafety"
        />
        <ScrollSelect
          class="select"
          placeholder=""
          :options="testList"
          v-model="form.safetyOfficer"
        />
        <ScrollSelect
          class="select"
          placeholder=" "
          :options="testList"
          v-model="form.date"
        />
      </div>
     <div class="buttons">
        <button
          class="buttons__add-instruction basic-button"
          @click="showInstruction = !showInstruction"
          v-if="!showInstruction"
        >
           
        </button>
        <button
          class="buttons__save-instruction basic-button"
          type="submit"
          @click="saveInstruction"
          v-if="showInstruction"
        >
         
        </button>
        <button
          class="buttons__save basic-button"
          type="submit"
        >
          
        </button>
     </div>
    </form>
  </div>
</template>

<script>
import ScrollSelect from '../../../ScrollSelect'
import CheckBox from '../../../CheckBox'

export default {
  components: {
    ScrollSelect,
    CheckBox
  },
  data: () => ({
    form: {
      typeOfSafety: '',
      safetyOfficer: '',
      date: ''
    },
    safetySheet: [
      {
        id: '1',
        isChecked: true,
        typeOfSafety: '  ',
        safetyOfficer: ' ',
        date: '18.02.2020'
      },
      {
        id: '2',
        isChecked: false,
        typeOfSafety: '   ',
        safetyOfficer: ' ',
        date: '20.11.2020'
      },
      {
        id: '3',
        isChecked: true,
        typeOfSafety: 'test',
        safetyOfficer: ' ',
        date: '18.02.2020'
      }
    ],
    testList: [
      '1',
      '2',
      '3'
    ],
    showInstruction: false
  }),
  methods: {
    saveInstruction () {
      this.showInstruction = !this.showInstruction
      console.log('Instruction saved!')
    },
    test () {
      console.log(this.form.date)
    }
  }
}
</script>

<style lang="scss" scoped>

.accident-prevention-container {
  display: block;
  font-size: 14px;
  text-transform: uppercase;
  padding-top: 40px;
  padding-bottom: 20px;
}

.instruction-from {
  box-sizing: border-box;
  display: grid;
  grid-template-columns: 3fr 2fr 1fr;
  column-gap: 28px;
  padding-top: 10px;
  padding-bottom: 10px;
  padding-left: 20px;
  padding-right: 20px;
}

.select ::v-deep .search-input {
  height: 50px;
  border-radius: 100px;
  border: 2px solid #e0e2e5;
  padding: 0 20px;
  font-size: 14px;
  font-weight: bold;
  text-transform: uppercase;
}

.select ::v-deep .selected {
  border: 2px solid #e0e2e5;
}

.select ::v-deep .search-input::-webkit-input-placeholder {
  text-transform: uppercase;
  font-weight: bold;
  font-size: 14px;
  color: #999da9;
}

.select ::v-deep .search-input:-ms-input-placeholder {
  text-transform: uppercase;
  font-weight: bold;
  font-size: 14px;
  color: #999da9;
}

.select ::v-deep .search-input::placeholder{
  text-transform: uppercase;
  font-weight: bold;
  font-size: 14px;
  color: #999da9;
}

.buttons {
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 20px 0;
  &__save {
    background-color: #44CC95;
    &:hover {
      background-color: #2fe099;
    }
    &:active {
      background-color: #1cfaa1;
    }
  }
  &__add-instruction {
    background-color: #F87762;
    &:hover {
      background-color: #f65e46;
    }
    &:active {
      background-color: #e93a20;
    }
  }
  &__save-instruction {
    background-color: #F87762;
    &:hover {
      background-color: #f65e46;
    }
    &:active {
      background-color: #e93a20;
    }
  }
}

.basic-button {
  height: 50px;
  padding: 0 32px;
  box-sizing: border-box;
  border: none;
  border-radius: 100px;
  color: white;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 14px;
  outline: none;
  cursor: pointer;
  margin: 0 8px;
  text-align: center;
}

.checkbox {
  display: block;
  bottom: 10px;
}

.safety-sheet-container {
  border-collapse: collapse;
  width: 100%;
  color: #323A52;
  background-color: white;
}

.safety-sheet-container td, .safety-sheet-container th {
  border-bottom: 1px solid #e0e2e5;
  padding-top: 12px;
  padding-bottom: 12px;
  padding-left: 20px;
  padding-right: 20px;
}

table tr:last-child td {
  border-bottom: 0;
}

.safety-sheet-container tr:hover {
  background-color: #f3f3f3;
}

.safety-sheet-container th {
  padding-top: 12px;
  padding-bottom: 12px;
  text-align: left;
  background-color: white;
}
</style>
