<style lang="scss" scoped>
.app-container {
  width: 100%;
  height: 100%;
  background-image: url('../../assets/background.svg');
  background-size: cover;
  background-repeat: no-repeat;
}

.container {
  display: grid;
  grid-template-columns: 20% 80%;
  height: 100%;
  overflow: hidden;
}

.content { // main content
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.under-navbar {
  margin-left: 40px;
}

.navbar {
  border-radius: 0 30px 30px 0;
  background-color: #323A52; // navbar bg
}

.tabs {
  font-size: 16px;
  font-weight: bold;
  text-transform: uppercase;
  color: white;
  margin-top: 124px;
  background-image:
    linear-gradient(
      to left,
      #F4F4F4 50%,
      #323A52 50%
    );
}

.tabs li:hover {
  color: #d1d1d1;
}

.tabs li {
  cursor: pointer;
  padding: 20px 12px; // same distance to align when tab is active and when not
  background-color: #323A52; // tab bg
  svg {
    fill: white;
  }
}

.active {
  padding: 0;
  margin: 0;
  border-radius: 30px 30px 30px 30px;
}

.tabs li.active {
  padding: 20px 12px; // same distance to align when tab is active and when not
  cursor: default;
  color: #323A52;
  border-radius: 30px;
  transition: background-color 0.3s;
  background-color: #F4F4F4;
  svg {
    fill: #323A52;
  }
}

.help-tab {
  font-size: 0;
  padding: 20px 0;
  color: transparent;
  pointer-events: none;
  background-color: #323A52;
