<script>
export default {
  props: {
    event: {
      type: String,
      required: true,
      default: function (name) {
        return [
          '',
          '',
          ' ',
          ' ',
          '  ',
          ' '
        ].indexOf(name) === -1
      }
    },
    workerName: {
      type: String,
      required: true
    },
    distance: {
      type: Number,
      required: true
    }
  }
}
</script>

<style lang="scss" scoped>
.worker-event-block {
  box-sizing: border-box;
  padding: 20px;
  width: 640px;
  border-radius: 16px;
  background-color: white;
  box-shadow: 0px 3.00674px 15.0337px rgba(101, 40, 30, 0.15);
}

.event-avatar {
  display: flex;
  align-items: center;
  text-align: center;
  justify-content: center;
  width: 65px;
  height: 65px;
  border-radius: 50%;
  font-weight: 600;
}

.event-info {
  padding-top: 6px;
}

.worker-name {
  color: #323A52;
  font-size: 16px;
  font-weight: bold;
}

.worker-location {
  &__location {
    color: #999DA9;
  }
}

.event-time {
  &__full-time {
    color: #999DA9;
  }
}

.event {
  display: inline;
  vertical-align: bottom;
  &__event-name {
    text-transform: lowercase;
    color: #999DA9;
  }
}

.center-align {
  display: inline-block;
  vertical-align: middle;
}

.text-top {
  vertical-align: text-top;
}

.action-event {
  background-color: white;
  border: 2.3px solid #999DA9;
}

.system-event {
  background-color: #FAAB1D;
  border: none;
}

.important-event {
  background-color: #F87762;
  border: none;
}
</style>
