<template>
  <div
    class="custom-select"
    :tabindex="tabindex"
    @blur="open = false"
  >
    <div
      class="selected"
      :class="{open: open}"
      @click="open = !open"
    >
      <span v-if="selected">{{ selected }}</span>
      <span v-else class="select-placeholder">{{ placeholder }}</span>
    <div class="selected-svg">
      <svg
        :class="{ 'rotated-up ': open, 'rotated-down': !open }"
        width="20"
        height="16"
        viewBox="0 0 11 7"
        fill="none"
        xmlns="http://www.w3.org/2000/svg"
      >
        <path
          d="M10.0202 1L5.51009 5.51011L0.999978 1"
          stroke="#323A52"
          stroke-opacity="0.5"
          stroke-width="0.751685"
        />
      </svg>
    </div>
    </div>
    <div
      class="items"
      :class="{ 'selectHide': !open }"
    >
      <div
        class="item"
        v-for="(option, i) of options"
        :key="i"
        @click="selected=option; open=false; $emit('input', option)"
      >
        {{ option }}
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    options: {
      type: Array,
      required: true
    },
    tabindex: {
      type: Number,
      required: false,
      default: 0
    },
    placeholder: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      selected: this.options.length > 0 ? this.options[this.placeholder ? -1 : 0] : null,
      open: false
    }
  },
  mounted () {
    this.$emit('input', this.selected)
  }
}
</script>

<style lang="scss" scoped>

.custom-select {
  position: relative;
  width: 100%;
  outline: none;
}

.selected {
  width: 100%;
  box-sizing: border-box;
  background-color: white;
  border-radius: 100px;
  color: black;
  text-transform: uppercase;
  padding-left: 20px;
  cursor: pointer;
}

.select-placeholder {
  color: #999da9;
}

.selected-svg {
  float: right;
  padding-right: 16px;
}

.items {
  z-index: 5;
  color: black;
  border-radius: 8px;
  margin-top: 4px;
  overflow: hidden;
  position: absolute;
  left: 0;
  right: 0;
  border: 1px solid #d6d8dc;
  background-color: white;
}

.item {
  color: black;
  padding-left: 8px;
  cursor: pointer;
  background-color: white;
}

.item:hover {
  background-color: #f3f3f3;
}

.selectHide {
  display: none;
}

.rotated-up {
  transform: rotate(180deg);
  transition: 0.2s ease all;
}

.rotated-down {
  transform: rotate(0deg);
  transition: 0.2s ease all;
}
</style>
