<style lang="scss" scoped>
$Dark-blue: #323A52;

.worker-card-container {
  width: 252px;
  border-radius: 10px;
  background-color: white;
  box-shadow: 0px 3.00674px 15.0337px
    rgba(101, 40, 30, 0.15);
  &__battery {
    float: right;
    margin-top: 8px;
    margin-right: 8px;
  }
  &__worker-name-info {
    display: grid;
    grid-template-columns: 33% 66%;
    margin-left: 14px;
    padding-top: 12px;
    padding-bottom: 4px;
    height: 64px;
    &__worker-name {
      color: $Dark-blue;
      font-size: 14px;
      font-weight: bold;
      padding-top: 20px;
      padding-left: 8px;
    }
    &__worker-avatar {
      &__photo {
        display: flex;
        align-items: center;
        text-align: center;
        justify-content: center;
        width: 60px;
        height: 60px;
        border-radius: 50%;
        color: $Dark-blue;
        text-transform: uppercase;
        font-weight: 600;
        font-size: 1.5rem;
        letter-spacing: 0.5px;
        border: 2px solid #999da9;
      }
      &__is-online {
        position: relative;
        left: 44px;
        bottom: 24px;
        width: 22px;
        height: 22px;
        border-radius: 50%;
      }
    }
  }
}

.worker-info {
  width: 100%;
  font-size: 20px;
  color: #999da9;
  border-collapse: collapse;
}

.worker-info td, .worker-info th {
  padding-top: 4px;
  padding-bottom: 4px;
  padding-left: 34px;
  border-bottom: 1px solid #ddd;
}

.worker-info tr:hover {
  background-color: #f3f3f3;
}

td svg {
  width: 28px;
  height: auto;
  margin-top: 4px;
}
</style>

<script>
export default {
  data: () => ({
    worker: {
      workerFullName: '  ',
      companyName: ' ',
      workerPosition: '',
      workerLocation: 4,
      workerPulse: 89,
      batteryCharge: 100,
      isOnline: false
    }
  }),
  methods: {},
  computed: {
    getInitials () {
      return this.worker.workerFullName
        .split(' ').splice(0, 2)
        .toString()
        .match(/[^-\s]/gm)
        .join()
        .replace(/[, ]+/g, '')
    }
  }
}
</script>
